<template>
  <div class="allPage">
    <div class="systemState">
      <el-form ref="form" :model="formData2" label-width="80px">
        <el-form-item label="系统开关" >
          <el-switch v-model="formData2.system_state" @change="systemStateChange"></el-switch>
        </el-form-item>
      </el-form>
    </div>
    <div class="form">
    <el-form :model="formData" :rules="rules" ref="formData" label-width="100px">
      <el-form-item label="课程名称" prop="course_name">
        <el-input v-model="formData.course_name"></el-input>
      </el-form-item>
      <el-form-item label="教师名称" prop="teacher_name">
        <el-input v-model="formData.teacher_name"></el-input>
      </el-form-item>
      <el-form-item label="课程时间" prop="course_week">
        <el-input v-model="formData.course_week" autocomplete="off" placeholder="格式为：1-16"></el-input>
      </el-form-item>
      <el-form-item label="学生人数" prop="course_student_num">
        <el-input type="number" v-model.number="formData.course_student_num" autocomplete="off"></el-input>
      </el-form-item>

      <el-form-item label="发布学院" prop="course_range_college">
        <el-select v-model="formData.course_range_college" placeholder="请选择发布学院" @change="collegeChange" style="width:100%;">
          <el-option v-for="(item ,index) in college_item"  :key="index" :value="index+1" :label="item">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="发布专业" prop="course_range_major">
        <el-select v-model="formData.course_range_major" placeholder="请选择发布专业"  style="width:100%;">
          <el-option v-for="(item ,index) in major_item[college_value]" :key="index"  :value="index+1" :label="item">
          </el-option>
        </el-select>
      </el-form-item>

      <el-form-item>
        <el-button type="primary" @click="submitForm('formData')">立即上传</el-button>
        <el-button @click="resetForm('formData')">重置</el-button>
      </el-form-item>
    </el-form>
    </div>
  </div>
</template>

<script>
  import {get_num_code} from "../../axios/api";
  import {open_elective_system} from "../../axios/api";
  import {off_elective_system} from "../../axios/api";
  import {get_elective_system_state} from "../../axios/api";
  import {insert_new_class} from "../../axios/api";

  export default {
      name: "addClass",
      data(){
          return{
            formData2:{
              system_state:false,
            },
            formData: {
              course_name:"",  //课程名称
              teacher_name:"", //教师名称
              course_week:"",  //课程时间
              course_student_num:"", //招生人数
              course_range_college:"",  //学院码
              course_range_major:"", //专业码（0就是全部专业）
            },
            rules: {
              course_name:[
                { required: true, message: '名称不能为空'}
              ],
              teacher_name:[
                { required: true, message: '名称不能为空'}
              ],
              course_week:[
                { required: true, message: '时间不能为空'}
              ],
              course_student_num:[
                { required: true, message: '人数不能为空'},{ type: 'number', message: '人数必须为数字值'}
              ],
              course_range_college:[
              { required: true, message: '请选择发布学院'}
              ]
            },
            college_item:[],//存贮学院名称
            major_item:[],//存储专业信息
            college_value:"",
            major_value:"",
          }
      },
      created:function () { //创建声明周期函数
        //get collage/major Num
        let that=this;
        get_num_code().then(res=>{
          this.college_item=res.data[0];
          for(let i=0;i<res.data[0].length;i++){
            that.major_item.push(res.data[i+1]);
          }
        })
        get_elective_system_state().then(res=>{
          console.log(res.data);
          if(res.data===0){
            this.formData2.system_state=false
          }else if(res.data===1){
            this.formData2.system_state=true
          }
        })

      },
      methods:{
        systemStateChange(){
          if(this.formData2.system_state===false){
            this.offSystem();
            console.log("guanbi")
          }else {
            this.openSystem();
          }
        },
        openSystem(){
          let that =this;
          let cache=that.formData2.system_state;
          open_elective_system().then(res=>{
            console.log(res);
            if(res.data===1){
              this.$notify({
                title: '成功',
                message: '您已成功开启系统',
                type: 'success',
                duration:'2000'
              });
              that.formData2.system_state=true;
            }else{
              //失败
              that.formData2.system_state=cache;
              this.$notify.error({
                title: '错误',
                message: '操作失败',
                duration:'2000'
              });
            }
          })
        },
        offSystem(){
          let that =this;
          let cache=that.formData2.system_state;
          off_elective_system().then(res=>{
            if(res.data===1){
              this.$notify({
                title: '成功',
                message: '您已成功关闭系统',
                type: 'success',
                duration:'2000'
              });
              that.formData2.system_state=false;
            }else{
              //失败
              that.formData2.system_state=cache;
              this.$notify.error({
                title: '错误',
                message: '操作失败',
                duration:'2000'
              });
            }
          })
        },
        submitForm(formName) {
          if(!this.formData.course_range_major){
            this.formData.course_range_major=0
          }
          this.$refs[formName].validate((valid) => {
            if (valid) {
              insert_new_class(this.formData).then(res=>{
                this.$notify({
                  title: '成功',
                  message: '您已成功上传',
                  type: 'success',
                  duration:'2000'
                });
              })
            } else {
              console.log('error submit!!');
              this.$notify.error({
                title: '错误',
                message: '操作失败',
                duration:'2000'
              });
            }
          });
        },
        resetForm(formName) {
          this.$refs[formName].resetFields();
        },
        collegeChange(){
          console.log(this.formData.course_range_college)
          this.college_value=this.formData.course_range_college-1
        }
      }
    }
</script>

<style scoped>
.allPage {
 width:100%;
  height:100%;
}
  .systemState {
    position: absolute;
    top:0;
    right:50px;
  }
  .allPage >.form {
    width:30%;
    margin: 40px auto 0;
  }
</style>
<!--
步骤：前端向后端提交表单，目测就ok，后端只需返回成功与否，
表单包括：
  1.课程名称
  2.教师名称
  3.课程周数
  4.招生人数
  5.招生范围（学院，班级）

  附加一个开启和关闭选课系统的入口。
-->
<!--
  文件一：
    postData0:
        noData
      returnData:
        0 //系统关闭中
        1 //系统开启中
    postData1:
        systemState:1 //开启系统（num）
        systemState:0 //关闭系统（num）
      returnData:
        0 //开启失败
        1 //开启成功
    文件二：
    postData0:
        course_name:""  //课程名称
        teacher_name:"" //教师名称
        course_week:""  //课程时间
        course_student_num:"" //招生人数
        course_range_college:""  //学院码
        course_range_major:"" //专业码（0就是全部专业）
      returnData:
        0 //发布失败
        1 //发布成功
    文件三
    postData0:
        noData
      returnData: //对象里面套对象，外层对象0.放学院名称，1及以后放每个学院的专业名称
        {
          0:{
              1:"学院名"，
              2:"学院名"，
              3:"学院名"
              ...
            },
          1:{
              1:"专业名"，
              2:"专业名",
              3:"专业名"
              ...
            },
          2:{},
          ...
        }

        //优化版本，直接用数组套数组，注意顺序，就是第二个数组专业对应第一个学院
        [
          [
            "学院名1","学院名2","学院名3","学院名4"...
          ],
          [
            "专业名1","专业名2","专业名3","专业名4"...
          ]
        ]
-->



