<template>
    <div>
        <el-button @click="getStuListTo()" >下载学生模板</el-button>
        <el-button @click="getTeaListTo()" >下载教师模板</el-button>
        <postCsv></postCsv>
    </div>
</template>


<script>
  import postCsv from './postCsv'


    export default {
        name: "addStuOnce",
      data(){
          return{

          }
      },
      components:{
         postCsv:postCsv
      },
      methods:{

        //  下载表单
        getStuListTo() {
            window. open ("http://144.202.108.147/information/upload_students.csv")
        },
        getTeaListTo(){
            window. open ("http://144.202.108.147/information/upload_teachers.csv")
        },
      }
    }
</script>

<style scoped>
div {
  margin-top:30px;
}
</style>
