import {api} from "./fetch"

//login
export  function logIn(userId,userPass) {
  console.log(userId, userPass)
  return api.post('/login/select.php', {id:userId,pass:userPass})
}

//成绩查询
export  function chickTestQuery(key) {
  console.log(key)
  console.log("1")
  return api.post('/score/score_query.php', {key:key})
}

// 添加学生
export  function submitStudent(form) {
  return api.post('/information/student_insert.php',form)
}

//添加教师
export  function submitTeacher(form) {
  return api.post('/information/teacher_insert.php',form)
}

//接收修改信息
export  function amemtMsg(number) {
  return api.post('/information/loadinf.php',{"id":number})
}

//发送修改信息
export  function sentMsg(formed) {
  return api.post('/information/inf_update.php',formed)
}

//批量上传 学生/教师
export  function sentCsv(formData,type) {
  return api.post('/information/UploadInfFile_' + type + '.php' ,formData)
}

//批量上传成绩
export  function sentMark(formData) {
 return api.post('/score/UploadScoreFile.php' , formData)
}

//查询课表
export  function timeTable(userId,week) {
  return api.post('http://144.202.108.147/course/course_table.php',{num:userId,week:week})
}

//查询选课记录
export function get_elective(userId) {
  return api.post('http://144.202.108.147/course/course_select.php',{userId:userId})
}

//选择课程
export function choose_elective(userId,chooseClassId) {
  return api.post('http://144.202.108.147/course/course_select.php',{userId:userId,chooseClassId:chooseClassId})
}

//重选课程
export function cancel_elective(userId,cancelClassId) {
  return api.post('http://144.202.108.147/course/course_select.php',{userId:userId,cancelClassId})
}

//获取学院和专业信息
export function get_num_code() {
  return api.post('http://144.202.108.147/course/getDepMajor.php',{})
}

//开启选课系统
export function open_elective_system() {
  return api.post('http://144.202.108.147/course/systemOnOff.php',{systemState:1})
}

//关闭选课系统
export function off_elective_system() {
  return api.post('http://144.202.108.147/course/systemOnOff.php',{systemState:0})
}

//查看查克系统状态
export function get_elective_system_state() {
  return api.post('http://144.202.108.147/course/systemOnOff.php',{})
}

//插入新的课程信息
export function insert_new_class(formData)
{
  return api.post('http://144.202.108.147/course/course_add.php',formData)
}











