<template>
  <div class="hello">
    <div class="box_1">
      <el-button @click="switchUpdate('stu')">批量上传学生</el-button>
      <el-button @click="switchUpdate('tea')">批量上传教师</el-button>
    </div>
    <div class="box_2">
      <p v-if="type=='stu'">请上传学生信息模板文件</p>
      <p v-if="type=='tea'">请上传教师信息模板文件</p>
      <div v-if="type.length!==0">
        <form>
          <input type="file" @change="getFile($event)">
          <button class="button button-primary button-pill button-small" @click="submit($event)">提交</button>
        </form>
      </div>
    </div>
    <!--<div>-->
      <!--<form>-->
        <!--<input type="file" @change="getFile($event)">-->
        <!--<button class="button button-primary button-pill button-small" @click="submit($event)">提交</button>-->
      <!--</form>-->
    <!--</div>-->
  </div>

</template>

<script>
  import {sentCsv} from "../../axios/api";



  export default {
    name: 'postCsv',
    data() {
      return {
        type:'' ,
        file: ''
      }
    },
    methods: {
      //改变接口
      switchUpdate(userType){
        this.type = userType
      },


      //引入文件
      getFile: function (event) {
        this.file = event.target.files[0];
        console.log(this.file);
      },
      submit: function (event) {
        event.preventDefault();
        let formData = new FormData();
        formData.append("file", this.file);
         this.postCsvTo(formData,this.type)
        },
      //接口
      postCsvTo(formData,type){
        let that=this;
        sentCsv(formData,type)
          .then(function (response) {
            that.$notify({
              title: '成功',
              message: '您已成功上传',
              type: 'success',
              duration:'2000'
            });
            console.log(response);
            window.location.reload();
          })
          .catch(function (error) {
            that.$notify.error({
              title: '错误',
              message: '操作失败',
              duration:'2000'
            });
            console.log(error);
            window.location.reload();
          });
      },

    }
  }
</script>

<style scoped>
.box_1,box_2 {
  margin: 30px 0 30px 0;

}
</style>



