<template>
  <div class="allPage">
    <div class="error"
      v-if="error"
    >
      <img src="../../assets/error.png" alt="">
      <div>现在不是选课时间呦！！</div>
    </div>
    <div class="elective" v-if="!error" v-loading="loading">
      <div class="title">未选的课</div>
      <el-table :data="electiveData">
        <el-table-column width="250" property="course_name" label="课程名称"></el-table-column>
        <el-table-column width="250" property="course_week" label="教学时间"></el-table-column>
        <el-table-column width="250" property="teacher_name" label="讲师姓名"></el-table-column>
        <el-table-column width="250" property="course_surplus_num" label="剩余人数"></el-table-column>
        <el-table-column width="250" property="course_student_num" label="学生人数"></el-table-column>
        <el-table-column width="250" property="course_state" label="课程状态"></el-table-column>
        <el-table-column
          fixed="right"
          label="操作"
          width="180">
          <template slot-scope="scope">
            <!--@click.native.prevent="deleteRow(scope.$index, tableData4)"-->
            <el-button
              type="text"
              size="small"
              @click.native.prevent="choose_class(160121712,scope.$index)"
            >
              选择
            </el-button>
          </template>
        </el-table-column>
      </el-table>
      <div class="title">已选的课</div>
      <el-table :data="electiveOverData">
        <el-table-column width="250" property="course_name" label="课程名称"></el-table-column>
        <el-table-column width="250" property="course_week" label="教学时间"></el-table-column>
        <el-table-column width="250" property="teacher_name" label="讲师姓名"></el-table-column>
        <el-table-column
          fixed="right"
          label="操作"
          width="180">
          <template slot-scope="scope">
            <el-button
              type="text"
              size="small"
              @click.native.prevent="cancal_class(160121712,scope.$index)"
            >
              取消
            </el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
  import {get_elective} from "../../axios/api"
  import {choose_elective} from "../../axios/api";
  import {cancel_elective} from "../../axios/api";

  export default {
        name: "elective",
      data(){
        return{
          electiveData:[],
          electiveOverData:[],
          error:false,//选课错误界面
          loading:false
        }
      },
      created: function() {     //创建界面生命周期函数
          let that=this;
           that.loading=true;
          get_elective(160121712).then(res=>{
            // console.log(res.data);
            if(res.data===0){//现在不是选课时间
              that.loading=false;
              that.electiveData=[];
              that.electiveOverData=[];//清除内容
              this.$alert('现在还不是选课时间', '警告', {   //弹窗警告
                confirmButtonText: '我知道了',
              });
              that.error=true;
            }else{
              let elective_courseId=[];//存储已经选的课的Id
              for(let i=0;i<res.data[1].length;i++){  //取出已选的课的Id
                elective_courseId.push(res.data[1][i].course_id)
              }
              for(let i = res.data[0].length-1;i>=0;i--){  //倒序判断未选的课的Id是否已选，是就删除
                if(elective_courseId.indexOf(res.data[0][i].course_id)>-1 )res.data[0].splice(i, 1);
              }
              that.electiveData=res.data[0];
              that.electiveOverData=res.data[1];
              that.loading=false;
            }
          })
      },
      methods: {
        choose_class(userId,index){
          let chooseClassId=this.electiveData[index].course_id;
          let that=this;
          that.loading=true;
          choose_elective(userId,chooseClassId).then(res=>{
            console.log(res.data);

            if(res.data===2){//该课程人数已满
              get_elective(160121712).then(res=>{
                // console.log(res.data);
                if(res.data===1){//现在不是选课时间
                  that.loading=false;
                  that.electiveData=[];
                  that.electiveOverData=[];//清除内容
                  this.$alert('现在还不是选课时间', '警告', {   //弹窗警告
                    confirmButtonText: '我知道了',
                  });
                  that.error=true;
                }else{
                  let elective_courseId=[];//存储已经选的课的Id
                  for(let i=0;i<res.data[1].length;i++){  //取出已选的课的Id
                    elective_courseId.push(res.data[1][i].course_id)
                  }
                  for(let i = res.data[0].length-1;i>=0;i--){  //倒序判断未选的课的Id是否已选，是就删除
                    if(elective_courseId.indexOf(res.data[0][i].course_id)>-1 )res.data[0].splice(i, 1);
                  }
                  that.electiveData=res.data[0];
                  that.electiveOverData=res.data[1];
                  that.loading=false;
                }
              })
              this.$alert('对不起，该课程已满', '警告', {   //弹窗警告
                confirmButtonText: '我知道了',
              });
            }else{
              this.$notify({
                title: '成功',
                message: '您已成功选择',
                type: 'success',
                duration:'2000'
              });
              let elective_courseId=[];//存储已经选的课的Id
              for(let i=0;i<res.data[1].length;i++){  //取出已选的课的Id
                elective_courseId.push(res.data[1][i].course_id)
              }
              for(let i = res.data[0].length-1;i>=0;i--){  //倒序判断未选的课的Id是否已选，是就删除
                if(elective_courseId.indexOf(res.data[0][i].course_id)>-1 )res.data[0].splice(i, 1);
              }
              that.electiveData=res.data[0];
              that.electiveOverData=res.data[1];
              that.loading=false;
            }
          })
        },
        cancal_class(userId,index){
          let cancelClassId=this.electiveOverData[index].course_id;
          let that =this;
          that.loading=true;
          cancel_elective(userId,cancelClassId).then(res=>{
            console.log(res.data);

            if (res.data===3){ //该课程已被锁定
              that.loading=false;
              this.$alert('对不起，该课程已被锁定', '警告', {   //弹窗警告
                confirmButtonText: '我知道了',
              });
            }else{
              this.$notify({
                title: '成功',
                message: '您已成功取消',
                type: 'success',
                duration:'2000'
              });
              let elective_courseId=[];//存储已经选的课的Id
              for(let i=0;i<res.data[1].length;i++){  //取出已选的课的Id
                elective_courseId.push(res.data[1][i].course_id)
              }
              for(let i = res.data[0].length-1;i>=0;i--){  //倒序判断未选的课的Id是否已选，是就删除
                if(elective_courseId.indexOf(res.data[0][i].course_id)>-1 )res.data[0].splice(i, 1);
              }
              that.electiveData=res.data[0];
              that.electiveOverData=res.data[1];
              that.loading=false;
            }
          })
        }
      }
    }
</script>

<style scoped>
  .error {
    height:100%;
    width:100%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .error div {
    margin-top: 30px;
    font-size:30px;
    font-weight: lighter;

  }

  .error img {
    height: 500px;
  }
  .allPage {
    width:100%;
    height:100%;
  }
.elective {
  width:100%;
  height:100%;

}
  .title {
    font-size: 30px;
    text-align: left;
    padding-left: 30px;
    color:#10aeff;
  }
</style>
<!--
    步骤:获取全部可以选的课以及已经选过的课,然后点击选课之后消息发送给后端,后端判断是否成功选课,成功就返回更新之后的课
         表,失败返回错误值.
-->


<!--
    return_data:
    [ //最外侧的数组包含两个小数组，第一个是要选的课，另一个是选完的课
      [ //要选的课
        {
          course_id:"",//课程id
          course_name:"",  //课程名称
          course_week:"",  //课程周数，例：“1-5，7-8”
          teacher_name:"", //教师名称
          course_surplus_num:"", //剩余人数
          course_student_num:"", //总人数
          course_state:""        //课程状态
        },{
          course_id:"",
          course_name:"",
          course_week:"",
          teacher_name:"",
          course_surplus_num:"",
          course_student_num:"",
          course_state:""
        }
      ],
      [  //选完的课
        {
          course_id:"",//课程id
          course_name:"",  //课程名称
          course_week:"",  //课程周数
          teacher_name:"",  //教室名称
        },{
          course_id:"",
          course_name:"",
          course_week:"",
          teacher_name:"",
        }
      ]
    ]

    postData1:
        userId

        success:return return_data
        fail:return 1 //现在不是选课时间

    postData2:
        userId
        chooseClassId

        success:return return_data
        fail:return 2 //该课程人数已满

    postData3:
        userId
        cancelClassId

        success:return return_data
        fail:return 3 //该课已被锁定，不可更改

-->



