          value: '',
          alert_ctrl:false,
          loading: false,
          return_cache:'',
          alert_item_cache:[],
          alert_item:[],
          schedule_Monday:[],
          schedule_Tuesday :[],
          schedule_Wednesday:[],
          schedule_Thursday:[],
          schedule_Friday:[],
          schedule_Saturday:[],
