             this.$message({
             type: 'info',
             message: '已取消删除'
             });
           */
          const that = this;

          this.loading = true;      //开始显示加载
          /*
            setTimeout(() => {
            this.loading = false;
            }, 1000);
          */

          timeTable(160121701,that.value).then(res=>{ //对返回的东西进行排序
            //测试用
            // console.log(res);
            console.log(res.data.length);
            console.log(res.data[0].length);
            // console.log(res.data[1].length);
            // console.log(res.data[0][1].chapter);

            //清空数组
            that.schedule_Monday.splice(0,that.schedule_Monday.length);
            that.schedule_Tuesday.splice(0,that.schedule_Tuesday.length);
            that.schedule_Wednesday.splice(0,that.schedule_Wednesday.length);
            that.schedule_Thursday.splice(0,that.schedule_Thursday.length);
            that.schedule_Friday.splice(0,that.schedule_Friday.length);
            that.schedule_Saturday.splice(0,that.schedule_Saturday.length);
            that.schedule_Sunday.splice(0,that.schedule_Sunday.length);

            //返回数据格式
            /*
            chapter: "3"
            classroom: "2-903"
            course_id: "6"
            course_name: "liangzilixue"
            days: "1"
            department: "li"
            semester: "2018-2019"
            student_class: "1601217"
            student_id: "160121701"
            teacher_name: "yanghui"
            weeks: "1"
            */

            that.alert_ctrl = res.data[0].length === 0;//判断是否显示此周无课
            that.search_show=true;//使查询按钮不可点击，防止重复查询占用资源


            //初始化数组
            for(let i=0; i<6; i++){
              that.schedule_Monday.push({chapter:"",classroom:"",course_id:"",course_name:"",days:"",department:"",semester:"",student_class:"",student_id:"",teacher_name:"",weeks:""});
              that.schedule_Tuesday.push({chapter:"",classroom:"",course_id:"",course_name:"",days:"",department:"",semester:"",student_class:"",student_id:"",teacher_name:"",weeks:""});
              that.schedule_Wednesday.push({chapter:"",classroom:"",course_id:"",course_name:"",days:"",department:"",semester:"",student_class:"",student_id:"",teacher_name:"",weeks:""});
              that.schedule_Thursday.push({chapter:"",classroom:"",course_id:"",course_name:"",days:"",department:"",semester:"",student_class:"",student_id:"",teacher_name:"",weeks:""});
              that.schedule_Friday.push({chapter:"",classroom:"",course_id:"",course_name:"",days:"",department:"",semester:"",student_class:"",student_id:"",teacher_name:"",weeks:""});
              that.schedule_Saturday.push({chapter:"",classroom:"",course_id:"",course_name:"",days:"",department:"",semester:"",student_class:"",student_id:"",teacher_name:"",weeks:""});
              that.schedule_Sunday.push({chapter:"",classroom:"",course_id:"",course_name:"",days:"",department:"",semester:"",student_class:"",student_id:"",teacher_name:"",weeks:""});
            }

            //空数组
            //chapter:"",classroom:"",course_id:"",course_name:"",days:"",department:"",semester:"",student_class:"",student_id:"",teacher_name:"",weeks:""


            //排序
            for(let i=0; i<res.data.length; i++){
              for (let j=0;j<res.data[i].length;j++){
                switch (res.data[i][j].days) {//判断周几
                  case "1": {
                    switch (res.data[i][j].chapter) {//判断节数
                      case "1": //周一第一节
                        that.schedule_Monday[0]=res.data[i][j];
                        break;
                      case "2": //周一第二节
                        that.schedule_Monday[1]=res.data[i][j];
                        break;
                      case "3": //周一第三节
                        that.schedule_Monday[2]=res.data[i][j];
                        break;
                      case "4": //周一第四节
                        that.schedule_Monday[3]=res.data[i][j];
                        break;
                      case "5": //周一第五节
                        that.schedule_Monday[4]=res.data[i][j];
                        break;
                      case "6": //周一第六节
                        that.schedule_Monday[5]=res.data[i][j];
                        break;
                    }
                    break;
                  }

                  case "2": {
                    switch (res.data[i][j].chapter) {//判断节数
                      case "1": //周二第一节
                        that.schedule_Tuesday[0]=res.data[i][j];
                        break;
                      case "2": //周二第二节
                        that.schedule_Tuesday[1]=res.data[i][j];
                        break;
                      case "3": //周二第三节
                        that.schedule_Tuesday[2]=res.data[i][j];
                        break;
                      case "4": //周二第四节
                        that.schedule_Tuesday[3]=res.data[i][j];
                        break;
                      case "5": //周二第五节
                        that.schedule_Tuesday[4]=res.data[i][j];
                        break;
                      case "6": //周二第六节
                        that.schedule_Tuesday[5]=res.data[i][j];
                        break;
                    }
                    break;
                  }

                  case "3": {
                    switch (res.data[i][j].chapter) {//判断节数
                      case "1": //周三第一节
                        that.schedule_Wednesday[0]=res.data[i][j];
                        break;
                      case "2": //周三第二节
                        that.schedule_Wednesday[1]=res.data[i][j];
                        break;
                      case "3": //周三第三节
                        that.schedule_Wednesday[2]=res.data[i][j];
                        break;
                      case "4": //周三第四节
                        that.schedule_Wednesday[3]=res.data[i][j];
                        break;
                      case "5": //周三第五节
                        that.schedule_Wednesday[4]=res.data[i][j];
                        break;
                      case "6": //周三第六节
                        that.schedule_Wednesday[5]=res.data[i][j];
                        break;
                    }
                    break;
                  }

                  case "4": {
                    switch (res.data[i][j].chapter) {//判断节数
                      case "1": //周四第一节
                        that.schedule_Thursday[0]=res.data[i][j];
                        break;
                      case "2": //周四第二节
                        that.schedule_Thursday[1]=res.data[i][j];
                        break;
                      case "3": //周四第三节
                        that.schedule_Thursday[2]=res.data[i][j];
                        break;
                      case "4": //周四第四节
                        that.schedule_Thursday[3]=res.data[i][j];
                        break;
                      case "5": //周四第五节
                        that.schedule_Thursday[4]=res.data[i][j];
                        break;
                      case "6": //周四第六节
                        that.schedule_Thursday[5]=res.data[i][j];
                        break;
                    }
                    break;
                  }

                  case "5": {
                    switch (res.data[i][j].chapter) {//判断节数
                      case "1": //周五第一节
                        that.schedule_Friday[0]=res.data[i][j];
                        break;
                      case "2": //周五第二节
                        that.schedule_Friday[1]=res.data[i][j];
                        break;
                      case "3": //周五第三节
                        that.schedule_Friday[2]=res.data[i][j];
                        break;
                      case "4": //周五第四节
                        that.schedule_Friday[3]=res.data[i][j];
                        break;
                      case "5": //周五第五节
                        that.schedule_Friday[4]=res.data[i][j];
                        break;
                      case "6": //周五第六节
                        that.schedule_Friday[5]=res.data[i][j];
                        break;
                    }
                    break;
                  }

                  case "6": {
                    switch (res.data[i][j].chapter) {//判断节数
                      case "1": //周六第一节
                        that.schedule_Saturday[0]=res.data[i][j];
                        break;
                      case "2": //周六第二节
                        that.schedule_Saturday[1]=res.data[i][j];
                        break;
                      case "3": //周六第三节
                        that.schedule_Saturday[2]=res.data[i][j];
                        break;
                      case "4": //周六第四节
                        that.schedule_Saturday[3]=res.data[i][j];
                        break;
                      case "5": //周六第五节
                        that.schedule_Saturday[4]=res.data[i][j];
                        break;
                      case "6": //周六第六节
                        that.schedule_Saturday[5]=res.data[i][j];
                        break;
                    }
                    break;
                  }

                  case "7": {
                    switch (res.data[i][j].chapter) {//判断节数
                      case "1": //周日第一节
                        that.schedule_Sunday[0]=res.data[i][j];
                        break;
                      case "2": //周日第二节
                        that.schedule_Sunday[1]=res.data[i][j];
                        break;
                      case "3": //周日第三节
                        that.schedule_Sunday[2]=res.data[i][j];
                        break;
                      case "4": //周日第四节
                        that.schedule_Sunday[3]=res.data[i][j];
                        break;
                      case "5": //周日第五节
                        that.schedule_Sunday[4]=res.data[i][j];
                        break;
                      case "6": //周日第六节
                        that.schedule_Sunday[5]=res.data[i][j];
                        break;
                    }
                    break;
                  }
                }
              }
            }
            that.loading = false; //终止显示加载
          })
        },

      }
    }

</script>

